import React, { Component } from "react";
import { connect } from "react-redux";
import { setA11yContent } from "../NavigationHelper/actions";

class RouteThree extends Component {
  componentDidMount() {
    this.props.setA11yContent("route three");
  }

  render() {
    return <div>route three</div>;
  }
}

export default connect(null, { setA11yContent })(RouteThree);
