import React, { Component } from "react";
import { connect } from "react-redux";
import { setA11yContent } from "../NavigationHelper/actions";

class RouteOne extends Component {
  componentDidMount() {
    this.props.setA11yContent("route one");
  }

  render() {
    return <div>route one</div>;
  }
}

export default connect(null, { setA11yContent })(RouteOne);
