import React, { Component } from "react";
import { connect } from "react-redux";
import { setA11yContent } from "../NavigationHelper/actions";

class RouteTwo extends Component {
  componentDidMount() {
    this.props.setA11yContent("route two");
  }

  render() {
    return <div>route two</div>;
  }
}

export default connect(null, { setA11yContent })(RouteTwo);
