import React, { Component } from "react";
import RouteOne from "./components/MockRoutes/RouteOne";
import RouteTwo from "./components/MockRoutes/RouteTwo";
import RouteThree from "./components/MockRoutes/RouteThree";
import { Route, Switch, Router, Link } from "react-router-dom";
import NavigationHelper from "./components/NavigationHelper/NavigationHelper";
import { createBrowserHistory } from "history";

const Routes = () => (
  <Router history={createBrowserHistory()}>
    <Link to="/one">one</Link>
    <Link to="/two">two</Link>
    <Link to="/three">three</Link>
    <NavigationHelper />
    <Switch>
      <Route exact path="/one" component={RouteOne} />
      <Route exact path="/two" component={RouteTwo} />
      <Route exact path="/three" component={RouteThree} />
    </Switch>
  </Router>
);

class App extends Component {
  render() {
    return (
      <div className="App">
        <div style={{ height: "100px", width: "100px", background: "blue" }} />
        <Routes />
      </div>
    );
  }
}

export default App;
